import api from "../api";
export default {
  data() {
    return {
      currentIndex: "chinaMap"
    };
  },

  methods: {
    getIndexHandle(index) {
      this.currentIndex = index;
    }

  },

  mounted() {
    // 国内
    api.getProvinceNcov().then(res => {
      let allCitys = [];

      if (res.data.message == "操作成功!") {
        for (let i = 0; i < res.data.retdata.length; i++) {
          var temp = {
            name: res.data.retdata[i].xArea,
            value: res.data.retdata[i].curConfirm
          };
          allCitys.push(temp);
        }

        this.$charts.chinaMap("map", allCitys);
      }
    }).catch(error => {
      console.log(error);
    }); // 世界

    api.getWorldNcov().then(res => {
      // console.log(res.data);
      let allCitys = [];

      if (res.data.message == "操作成功!") {
        for (let i = 0; i < res.data.retdata.length; i++) {
          var temp = {
            name: res.data.retdata[i].xArea,
            value: res.data.retdata[i].curConfirm
          };
          allCitys.push(temp);
        }

        this.$charts.worldMap("worldMap", allCitys);
      }
    }).catch(error => {
      console.log(error);
    });
  }

};