export default {
  name: "Content",
  props: {
    pans: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },

  render() {
    const h = arguments[0];
    return h("div", [this.pans.map((ele, index) => {
      // return  ele.isActive ? ele.$slots.default : ""
      return h("div", {
        "style": {
          display: ele.isActive ? 'block' : 'none'
        }
      }, [ele.$slots.default]);
    })]);
  }

};