export default {
  props: {
    label: {
      type: String,
      default: "tab"
    },
    index: {
      type: [String, Number],
      default: 1
    }
  },
  name: "Tab",
  computed: {
    isActive() {
      return this.$parent.currentIndex == this.index;
    }

  },

  mounted() {
    // console.log(this.$parent.currentIndex);
    // this.$parent.pans.push(this.$slots.default)
    this.$parent.pans.push(this);
  },

  methods: {
    clickItemHandle() {
      // 改变currentIndex的值，就可以改变高亮
      // 事件回调
      this.$parent.getIndex(this.index);
    }

  },

  render() {
    const h = arguments[0];
    let classNames = {
      tab: true,
      active: this.isActive
    };
    return h("li", {
      "on": {
        "click": this.clickItemHandle
      },
      "class": classNames
    }, [this.label]);
  }

};