export default {
  data() {
    return {
      city: "",
      show: false,
      cascaderValue: ""
    };
  },

  props: {
    options: {
      type: Array,
      default: () => {
        return [];
      }
    },
    label: {
      type: String,
      default: ""
    }
  },
  methods: {
    onFinish({
      selectedOptions
    }) {
      this.show = false;
      this.city = selectedOptions.map(option => option.text).join("/");
      this.$emit("onValue", selectedOptions[1]);
    }

  }
};