import Content from "./content.vue";
export default {
  name: "Tabs",

  data() {
    return {
      // 所有内容部分的容器
      pans: []
    };
  },

  props: {
    currentIndex: {
      type: [String, Number],
      default: 1
    }
  },
  components: {
    Content
  },
  methods: {
    getIndex(index) {
      this.$emit("onIndex", index);
    }

  },

  render() {
    const h = arguments[0];
    return h("div", [h("ul", {
      "class": "tabs-header"
    }, [this.$slots.default]), h(Content, {
      "attrs": {
        "pans": this.pans
      }
    })]);
  }

};